const app = getApp()
import helper from '../common/helper.js';
// 录音部分--开始
const recorderManager = uni.getRecorderManager();
const innerAudioContext = uni.createInnerAudioContext();
innerAudioContext.autoplay = true;
// 录音部分--结束
export default {
	data() {
		return {
			formNode: {
				title: '',
				content: '',
				board_id: '',
				topic_id: ''
			},
			readOnly: false,
			formats: {},
			title: '',
			voicePath: '',
			htmlCon: '',
			options: null
		}
	},
	onLoad(options) {
		console.log(options);
		this.formNode.board_id = options.id;
		this.options = options;
		// 富文本部分
		uni.loadFontFace({
			family: 'Pacifico',
			source: 'url("https://sungd.github.io/Pacifico.ttf")'
		})
		
		// 录音部分
		recorderManager.onStop(res => {
			// 获取音频文件地址
			console.log('recorder stop' + JSON.stringify(res));
			this.voicePath = res.tempFilePath;
			// 上传音频文件
			this.upVoice()
		});
	},
	methods: {
		// 数据提交
		submit() {
			this.editorCtx.getContents({
				success: data => {
					// console.log('insert divider success')
					// console.log(data)
					// 获取富文本中的内容
					console.log(data.html)
					this.htmlCon = data.html
					// 上传数据
					this.upData()
				}
			})
		},
		upData() {
			if (this.title == '') {
				uni.showToast({
					title: '请输入标题'
				});
				return false
			}
			if (this.htmlCon == '') {
				uni.showToast({
					title: '请编辑帖子内容'
				});
				return false
			}
			uni.showLoading({
			  title: '发布中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/posts/send`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					board_id: this.options.id,  // 模块ID
					topic_id: '',  // 主体ID
					title: this.title,  // 帖子标题
					content: this.htmlCon,  // 帖子内容
					voice: this.voicePath  // 录音的文件路径
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.data.status_code == 200) {
						uni.showToast({
							title: res.data.message
						});
						// 返回上一页
						uni.navigateBack()
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
			
				}
			})
		},
		// 获取帖子标题
		getTitle(e) {
			this.title = e.detail.value
		},
		// |||||||||||||||||||||录音部分--开始|||||||||||||||||||||
		startRecord() {
			console.log('开始录音');
			uni.showToast({
				title: "录音中...",
				duration: 99999999,
				icon: 'loading'
			});
			recorderManager.start();
		},
		endRecord() {
			console.log('录音结束');
			uni.hideToast()
			uni.showToast({
				title: "结束录音",
				duration: 2000,
				icon: 'success'
			});
			recorderManager.stop();
		},
		playVoice() {
			console.log('播放录音');
			if (this.voicePath) {
				innerAudioContext.src = this.voicePath;
				innerAudioContext.play();
			}
		},
		// 音频上传
		upVoice() {
			console.log('=========================================')
			console.log(this.voicePath)
			uni.showLoading({
			  title: '语音上传中...',
				duration: 1000000
			});
			uni.uploadFile({
				url: `${helper.requestUrl}/posts/uploads`,
				filePath: this.voicePath,
				name: 'file',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// console.log(res)
					uni.hideLoading();
					res = helper.null2str(res)
					res = JSON.parse(res.data)
					if (res.status_code == 200) {
						this.voicePath = res.data.path
					} else {
						uni.showToast({
							title: '上传失败，请重新录音'
						});
					}
				}
			});
		},
		// |||||||||||||||||||||录音部分--结束|||||||||||||||||||||
		// |||||||||||||||||||||---以下---为富文本部分|||||||||||||||||||||
		readOnlyChange() {
			this.readOnly = !this.readOnly
		},
		onEditorReady() {
			uni.createSelectorQuery().select('#editor').context((res) => {
				// console.log(res.context)
				this.editorCtx = res.context
			}).exec()
		},
		undo() {
			this.editorCtx.undo()
		},
		redo() {
			this.editorCtx.redo()
		},
		format(e) {
			let {
				name,
				value
			} = e.target.dataset
			if (!name) return
			// console.log('format', name, value)
			this.editorCtx.format(name, value)
		},
		onStatusChange(e) {
			const formats = e.detail
			this.formats = formats
		},
		insertDivider() {
			this.editorCtx.insertDivider({
				success: function() {
					// console.log('insert divider success')
				}
			})
		},
		clear() {
			this.editorCtx.clear({
				success: function(res) {
					// console.log("clear success")
				}
			})
		},
		removeFormat() {
			this.editorCtx.removeFormat()
		},
		insertDate() {
			const date = new Date()
			const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
			this.editorCtx.insertText({
				text: formatDate
			})
		},
		insertImage() {
			uni.chooseImage({
				count: 1,
				// sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				// sourceType: ['album', 'camera'],
				success: res => {
					uni.showToast({
						title: '图片上传中',
						icon: 'loading'
					});
					Promise.all(
						res.tempFiles.map(item => {
							return new Promise((resolve, reject) => {
								uni.uploadFile({
									url: `${helper.requestUrl}/posts/uploads`,
									filePath: item.path,
									name: 'file',
									header: {
										authorization: app.globalData.token
									},
									success: res => {
										// console.log(res)
										resolve({
											path: JSON.parse(res.data).data
										});
									}
								});
							});
						})
					).then(e => {
						uni.hideToast();
						// this.imgInfo = e[0].path;
						// console.log(this.imgInfo, '图片')
						this.editorCtx.insertImage({
							src: e[0].path.path,
							alt: '图像',
							success: function() {
								// console.log('insert image success')
							}
						})
					}).catch(err => console.log(err));
				}
			});
		}
		// |||||||||||||||||||||---以上---为富文本部分|||||||||||||||||||||
	},
	
}
